.bg-body {
  background-color: var(--covid-body-background);
}

.border {
  border-width: 1px;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.font-body {
  font-family: var(--font-body);
}

.h-screen {
  height: 100vh;
}

.min-h-full {
  min-height: 100%;
}

html,
body,
main {
  min-height: 0;
  height: 100%;
  min-width: 0;
  width: 100%;
}

:root {
  --font-display: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  --font-body: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.covid-light-theme {
  --covid-default: #1a202c;
  --covid-primary: #805ad5;
  --covid-primary-100: #faf5ff;
  --covid-primary-200: #e9d8fd;
  --covid-primary-300: #d6bcfa;
  --covid-primary-400: #b794f4;
  --covid-primary-500: #9f7aea;
  --mui-primary-rgb: 128, 90, 213;
  --covid-secondary: #319795;
  --mui-secondary-rgb: 49, 151, 149;
  --covid-success: #38a169;
  --covid-error: #e53e3e;
  --covid-info: #3182ce;
  --covid-warning: #dd6b20;
  --covid-link: #cbd5e0;
  --covid-link-active: #2d3748;
  --covid-nav-link: #f7fafc;
  --covid-nav-link-active: #4299e1;
  --covid-header-background: #3c366b;
  --covid-body-background: #edf2f7;
  --covid-card-background: #f7fafc;
  --covid-footer-background: #e2e8f0;
  --covid-on-primary: #faf5ff;
  --mui-on-primary-rgb: 250, 245, 255;
  --covid-on-secondary: #e6fffa;
  --mui-on-secondary-rgb: 230, 255, 250;
  --covid-on-header: #c3dafe;
  --covid-on-body: var(--covid-default);
  --covid-on-footer: #1a202c;
  --covid-btn-box-shadow: 0 0 2px 0 #718096;
  --covid-box-shadow-color: #a0aec0;
  --mui-icon-hover: rgba(26, 32, 44, 0.04);
  --covid-chart-grid: #cbd5e0;
  --covid-chart-tooltip-background: #2d3748;
  --covid-chart-tooltip-text: #edf2f7;
}

.covid-dark-theme {
  --covid-default: #cbd5e0;
  --covid-primary: #d53f8c;
  --covid-primary-100: #fff5f7;
  --covid-primary-200: #fed7e2;
  --covid-primary-300: #fbb6ce;
  --covid-primary-400: #f687b3;
  --covid-primary-500: #ed64a6;
  --mui-primary-rgb: 213, 63, 140;
  --covid-secondary: #dd6b20;
  --mui-secondary-rgb: 221, 107, 32;
  --covid-success: #38a169;
  --covid-error: #e53e3e;
  --covid-info: #319795;
  --covid-warning: #d69e2e;
  --covid-link: #fc8181;
  --covid-link-active: #9b2c2c;
  --covid-nav-link: #f7fafc;
  --covid-nav-link-active: #4299e1;
  --covid-header-background: #2d3748;
  --covid-body-background: #1a202c;
  --covid-card-background: #2d3748;
  --covid-footer-background: #4a5568;
  --covid-on-primary: #fff5f7;
  --mui-on-primary-rgb: 255, 245, 247;
  --covid-on-secondary: #fffaf0;
  --mui-on-secondary-rgb: 255, 250, 240;
  --covid-on-header: #edf2f7;
  --covid-on-body: var(--covid-default);
  --covid-on-footer: #f7fafc;
  --covid-btn-box-shadow: 0 0 2px 0 #f7fafc;
  --covid-box-shadow-color: #718096;
  --mui-icon-hover: rgba(203, 213, 224, 0.04);
  --covid-chart-grid: #4a5568;
  --covid-chart-tooltip-background: #edf2f7;
  --covid-chart-tooltip-text: #1a202c;
}
